CA,wCAEA,kBAAA,CADA,WAEA","sourcesContent":["<template>\r\n    <div>\r\n        <div v-if=\"errored\" class=\"alert alert-danger\" role=\"alert\">\r\n            Комментарии не загрузились!\r\n        </div>\r\n        <div v-else-if=\"posts.length > 0\" class=\"reviews\">\r\n            <div v-for=\"post in posts\" :key=\"post.id\" class=\"reviews__item\">\r\n                <div class=\"reviews__head\">\r\n                    <div class=\"reviews__name\">\r\n                        {{ post.title }}\r\n                    </div>\r\n                    <div class=\"reviews__date\">\r\n                        {{ post.created_at }}\r\n                    </div>\r\n                </div>\r\n                <div class=\"reviews__desk\">\r\n                    {{ post.description }}\r\n                </div>\r\n                <div class=\"reviews__del\">\r\n                    <button @click=\"deletePost(post.id)\" class=\"reviews__btn\"></button>\r\n                </div>\r\n            </div>\r\n        </div>\r\n        <div v-else class=\"review__empty\">\r\n            Отзывов пока нет) Будьте первым! 0/\r\n        </div>\r\n\r\n        <validation-errors v-if=\"validationErrors\" :errors=\"validationErrors\">\r\n        </validation-errors>\r\n        <form @submit.prevent=\"addPost\" class=\"addreview\">\r\n            <div class=\"addreview__name\">\r\n                <input v-model=\"post.title\" type=\"text\" name=\"name\" placeholder=\"Имя и Фамилия\">\r\n            </div>\r\n            <div class=\"addreview__text\">\r\n                <textarea v-model=\"post.description\" name=\"review\" id=\"\" cols=\"30\" rows=\"10\" placeholder=\"Комментарий\"></textarea>\r\n            </div>\r\n            <div class=\"addreview__btn\">\r\n                <button>Добавить комментарий</button>\r\n            </div>\r\n        </form>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    data() {\r\n        return {\r\n            posts: [],\r\n            post: {\r\n                id: '',\r\n                title: '',\r\n                description: ''\r\n            },\r\n            post_id: '',\r\n            edit: false,\r\n            loading: true,\r\n            errored: false,\r\n            validationErrors: ''\r\n        }\r\n    },\r\n    mounted() {\r\n        this.getPosts()\r\n    },\r\n    methods: {\r\n        getPosts() {\r\n            axios\r\n                .get('/api/posts')\r\n                .then(response => this.posts = response.data)\r\n                .catch(error => {\r\n                    console.log(error)\r\n                    this.errored = true;\r\n                })\r\n                .finally(() => this.loading = false)\r\n        },\r\n        deletePost(id) {\r\n            axios\r\n                .delete(`/api/posts/${id}`)\r\n                .then(response => {\r\n                    this.getPosts()\r\n                    console.log(response)\r\n                })\r\n                .catch(error => console.log(error))\r\n        },\r\n        addPost() {\r\n            axios\r\n                .post('/api/posts', {\r\n                    title: this.post.title,\r\n                    description: this.post.description,\r\n                })\r\n                .then(response => {\r\n                    this.post.title = ''\r\n                    this.post.description = ''\r\n                    this.getPosts()\r\n                    console.log(response)\r\n                    this.validationErrors = ''\r\n                })\r\n                .catch(error => {\r\n                    if (error.response.status === 422) {\r\n                        this.validationErrors = error.response.data.errors\r\n                    }\r\n                })\r\n        }   \r\n    },\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n.reviews {\r\n  max-width: 800px;\r\n  margin: 0 auto; \r\n  color: #273849;\r\n}\r\n.reviews__item {\r\n  padding: 15px;\r\n  background-color: #42b983;\r\n  margin-bottom: 25px;\r\n  border-radius: 20px;\r\n  position: relative;\r\n}\r\n.reviews__del button {\r\n    background-image: url(/images/trash_icon.svg);\r\n    width: 20px;\r\n    height: 20px;\r\n    position: absolute;\r\n    right: 16px;\r\n    top: 19px;\r\n    border: 0;\r\n    background-color: transparent;\r\n}\r\n.reviews__head {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    max-width: 700px;\r\n    margin: 0 auto;\r\n    padding: 10px;\r\n    font-weight: bold;\r\n}\r\n.reviews__desk {\r\n    max-width: 700px;\r\n    margin: 0 auto;\r\n    background-color: #fff;\r\n    padding: 20px;\r\n}\r\n.addreview {\r\n  padding: 15px;\r\n  background-color: #42b983;\r\n  margin-bottom: 25px;\r\n  border-radius: 20px;\r\n  position: relative;\r\n}\r\n.addreview {\r\n    max-width: 800px;\r\n    margin: 0 auto;\r\n}\r\n.addreview__name {\r\n  width: 300px;\r\n}\r\n.addreview__text {\r\n  padding: 10px;\r\n}\r\n.addreview__text textarea {\r\n  width: 100%;\r\n}\r\n.addreview__name input {\r\n    height: 25px;\r\n    padding: 10px;\r\n}\r\n.addreview__text textarea {\r\n    padding: 10px;\r\n    font-size: 16px;\r\n}\r\n.review__empty {\r\n    margin: 50px auto;\r\n    text-align: center;\r\n    font-size: 25px;\r\n    font-weight: bold;\r\n}\r\n.addreview__name, .addreview__btn {\r\n    margin-left: 10px;\r\n}\r\n.addreview__btn button {\r\n    padding: 5px;\r\n    border-radius: 10px;\r\n} \r\n</style>\r\n"],"sourceRoot":""}]);
